.contact-banner {
    background: 
    -webkit-gradient(
      linear, 
      left top, left bottom, 
      from(rgba(1, 13, 22, 0.60)), 
      to(rgba(1, 13, 22, 0.60))
    ),
    url("../images/luxe/yacht-zoom-1.jpeg") no-repeat center center fixed;
    background: 
    -webkit-linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("../images/luxe/yacht-zoom-1.jpeg") no-repeat center center fixed;
    background: 
    -o-linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("../images/luxe/yacht-zoom-1.jpeg") no-repeat center center fixed;
    background: 
    linear-gradient(
      rgba(1, 13, 22, 0.60), 
      rgba(1, 13, 22, 0.60)
    ),
    url("../images/luxe/yacht-zoom-1.jpeg") no-repeat center center fixed;
    background-size: cover;
    height: 30em;
    width: 100%;
}

.contact-description-container {
    padding: 7% 0% 0% 30%;
}

.contact-banner-description {
    font-family: 'Cinzel', serif;
    line-height: 75px;
    text-align: center;
    font-size: 48px;
    color: white;
    width: 500px;
}

.contact-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    font-size: 28px;
    color: black;
    font-family: 'Biryani', sans-serif;
    line-height: 50px;
    padding: 10% 10% 0% 10%;
}

.cert-group {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

.cert-image {
    margin: 4%;
    width: 15em;
    height: 8em;
}


@media (max-width: 768px) { 
    .contact-banner {
        background: 
        -webkit-gradient(
          linear, 
          left top, left bottom, 
          from(rgba(1, 13, 22, 0.60)), 
          to(rgba(1, 13, 22, 0.60))
        ),
        url("../images/luxe/yacht-zoom-1.jpeg") 50% 50%;
        background: 
        -webkit-linear-gradient(
          rgba(1, 13, 22, 0.60), 
          rgba(1, 13, 22, 0.60)
        ),
        url("../images/luxe/yacht-zoom-1.jpeg") 50% 50%;
        background: 
        -o-linear-gradient(
          rgba(1, 13, 22, 0.60), 
          rgba(1, 13, 22, 0.60)
        ),
        url("../images/luxe/yacht-zoom-1.jpeg") 50% 50%;
        background: 
        linear-gradient(
          rgba(1, 13, 22, 0.60), 
          rgba(1, 13, 22, 0.60)
        ),
        url("../images/luxe/yacht-zoom-1.jpeg") 50% 50%;
        height: 20em;
    }

    .contact-description-container {
        padding: 10% 0% 0% 15%;
    }

    .contact-banner-description {
        font-size: 32px;
    }

    .cert-group {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-line-pack: center;
            align-content: center;
    }
    
    .cert-image {
        margin: 4% 20%;
    }

}

@media (max-width: 480px) {
    
    .contact-description-container {
        padding: 10% 0% 0% -10%;
    }

    .contact-banner-description {
        text-align: left;
    }

    .contact-picture-group {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-line-pack: center;
            align-content: center;
    }

    .display-image {
        height: 15em !important;
    }
}


